













































































    import service from '../../service.js';
    import mInput from '@/components/m-input/m-input.vue';

    export default {
        components: {
            mInput
        },
        data() {
            return {
                account: '', //账号
                password: '', //密码
				repassword: '', //确认密码
				name:'', //用户姓名
				company:'', //企业名
				code: '', //组织机构代码
				idCard:'', //身份证
				phone:'', //手机号
                email: '', //电子邮件
				address:'', //收件地址
				array: ['个人用户', '企业用户'],
				index: 0,
            }
        },
        methods: {
			bindPickerChange: function(e) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.index = e.target.value
			},
            register() {
                /**
                 * 客户端对账号信息进行一些必要的校验。
                 * 实际开发中，根据业务需要进行处理，这里仅做示例。
                 */
				uni.showToast({
                    title: '注册成功'
                });
                // uni.navigateBack({
                //     delta: 1
                // });
				uni.reLaunch({
					url: '../login/login',
                });
                if (this.account.length < 5) {
                    uni.showToast({
                        icon: 'none',
                        title: '账号最短为 5 个字符'
                    });
                    return;
                }
                if (this.password.length < 6) {
                    uni.showToast({
                        icon: 'none',
                        title: '密码最短为 6 个字符'
                    });
                    return;
                }
                if (this.email.length < 3 || !~this.email.indexOf('@')) {
                    uni.showToast({
                        icon: 'none',
                        title: '邮箱地址不合法'
                    });
                    return;
                }

                const data = {
                    account: this.account,
                    password: this.password,
                    email: this.email
                }
                service.addUser(data);
                uni.showToast({
                    title: '注册成功'
                });
                uni.navigateBack({
                    delta: 1
                });
            }
        }
    }
